import Vue from 'vue'//重要
import Router from 'vue-router'//重要
//========================================
import Home from '../pages/home/home'
import Audios from '../pages/audio/audios'
import Find from '../pages/find/find'
import Room from '../pages/room/room'
import User from '../pages/user/user'
//================================================
//------Audios的内部链接------------------------------------------
import Movie from '../pages/audio/movie.vue'
import Movieitem from '../pages/audio/movieitem.vue'
import Read from '../pages/audio/read.vue'
import Tv from '../pages/audio/tv.vue'
import City from '../pages/audio/city.vue'
import Music from '../pages/audio/music.vue'
//------Home的内部链接------------------------------------------
import Homecount from '../pages/home/homecount'
import News1 from '../pages/home/news1'
import News2 from '../pages/home/news2'
//------下导航--------------------------------------
import Texttable from '../components/Texts'






Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      components:{
      	default:Home,
      	Texttable:Texttable
      }  
    },
    {
      path: '/home',
      name: 'Home',
      components:{
      	default:Home,
      	Texttable:Texttable
      }  
    },
    //-=---------
    {
    	path:"/news1/:id",
    	name:"News1",
    	component:News1
    },//注意components中是这么写的News1=News1；现在简写了，别忘了；
    {path:"/news2",name:"News2",component:News2},
    {path:"/movieitem/:id",name:"Movieitem",component:Movieitem},
    //----------------
     {
      path: '/audios',
      name: 'Audios',
      components:{ 
    		  default:Audios,
      	Texttable:Texttable
     },
      children:[
	      	{path: '',name: 'Movie',component:  Movie},
	      	{path: 'movie',name: 'Movie',component:  Movie},
		      {path: 'read',name: 'Read',component:  Read},
		      {path: 'tv',name: 'Tv',component:  Tv},
		      {path: 'city',name: 'City',component:  City},
		      {path: 'music',name: 'Music',component:  Music}
      	]
    },
     {
      path: '/find',
      name: 'Find',
      components:{
      	default:Find,
      	Texttable:Texttable
      }  
    },
     {
      path: '/room',
      name: 'Room',
      components:{
      	default:Room,
      	Texttable:Texttable
      }  
    },
     {
      path: '/user',
      name: 'User',
      components:{
      	default:User,
      	Texttable:Texttable
      }
    }
  ]
})



