<template>
	<li class="li-nav" @click="fn" :class="{green:!isbor}">{{txts}}</li>
</template>
<script type="text/javascript">
	export default {
		props:["txts","mark","sel"],
		data:function(){
			return{
				
			}
		},
		methods:{
			fn:function(){
				this.$emit("change",this.mark)
			}
		},
		components:{
		   			 
		},
		computed:{
			isbor:function(){
				if(this.mark==this.sel){
					return false;
				}
				return true;
			}
		}
	}
</script>
<style>
	.li-nav{float:left;width:20%;border-bottom: 0.02rem solid transparent;color:#9a9a9a;height:0.37rem;line-height: 0.37rem;text-align: center;font-size: 0.14rem;}
	.green{color:#9fe3a8;border-color:#9fe3a8 ;}
	/*导航部分样式*/
</style>


