<template>
	<div>
		<Titles class="titlewrap" bol="true">
				<input type="text" class="search" slot="inputsearch"  placeholder="影视	图书	唱片	小组等"/>
				<img src="../../assets/img/ic_search_gray.png" slot="searchImg" class="imgfirst"/>
				<img src="../../assets/img/ic_scan_gray.png" slot="boxImg" class="imgsecond"/>
				<img src="../../assets/img/ic_chat_white.png" slot="img2"  class="imgcheat">
				<!--<img src="../../assets/img/ic_search_green.png" slot="img1" class="imgsearch"  >
				<img src="../../assets/img/ic_chat_green.png" slot="img2" class="imgcheat"  ><!--不传入他-->
		</Titles>
		<Bannerdemo swipeid="swipeidHome" :autoplay="2000" paginationDirection="right">
			<div class="swiper-slide" slot="swiper-con">
				<img src="../../assets/img/images/banner/01.jpg"/>
			</div>
			<div class="swiper-slide" slot="swiper-con">
				<img src="../../assets/img/images/banner/02.jpg"/>
			</div>
			<div class="swiper-slide" slot="swiper-con">
				<img src="../../assets/img/images/banner/03.jpg"/>
			</div>
		</Bannerdemo>
		<Indexcounttile txt="热点" class="title_class"></Indexcounttile>
		<div class="content_wrap" v-for="item in nowNews">
			<router-link :to="{name:'News1',params:{id:item.id}}">
				<Indexitem :count="item.title" :point="item.content" :getid="item.id" mark="news1" last="" :writer="item.author" dels="" @thisid="getid"><img :src="item.image" slot="img3"/></Indexitem>
			</router-link>
		</div>
		<infinite-loading :on-infinite="onInfinite" ref="infiniteLoading">
			<span slot="no-more">没有更多数据了</span>
		</infinite-loading >
		<!--<Indexcount class="content_wrap"></Indexcount>-->
		<Indexcounttile txt="豆瓣时间" class="title_classtwo" style="background:#F7F7F7">
			<a href="##" slot="more" class="morething">更多专栏></a>
		</Indexcounttile>
		<Bannerdemo swipeid="swipeidtime" :autoplay="1000" :pagination="false" style="height:3.3rem;" :slidesPerView="1">
			<div class="swiper-slide" slot="swiper-con" v-for="item in hottimeArr">
				<Doubantime :title="item.title" :mintitle="item.mintitle" :peopletitle="item.peopletitle" :weekpeper="item.weekpeper" :content="item.content"  :author="item.author"  :url="item.url" ></Doubantime>
			</div>
		</Bannerdemo>
		<Indexcounttile txt="周末" class="title_classtwo"></Indexcounttile>
		<div class="content_wrap" v-for="item in weekgoArr">
			<Indexitem :count="item.count" :point="item.point" :writer="item.writer" mark="news1" :last="item.last" dels="">
				<img src="../../assets/img/430_03.jpg" slot="img3"/>
			</Indexitem>
		</div>

		<Indexcounttile txt="tutorabc" class="title_classthree">
			<img src="../../assets/img/131_06.jpg" slot="img" class="img">
			<a href="##" slot="adv" class="adv">广告</a>
			
		</Indexcounttile>
		<div class="content_wrap" v-for="item in languageArr">
			<Indexitem :count="item.count" :point="item.point" mark="news1" :last="item.last" :writer="item.writer" dels="">
				<img :src="item.url" slot="img3"/>
			</Indexitem>
		</div>

		
		<Indexcounttile txt="一刻" class="title_classtwo"></Indexcounttile>
		<div class="content_wrap" v-for="item in radioArr">
			<Indexitem :count="item.count" :point="item.point" mark="news1" :last="item.last" :writer="item.writer" dels="">
				<img :src="item.url" slot="img3"/>
			</Indexitem>
		</div>
		<Indexcounttile txt="发现" class="title_classtwo">
			<a href="##" slot="more" class="morething">更多专栏></a>
		</Indexcounttile>
		<Bannerdemo  swipeid="swipeidfind" :autoplay="1000" :pagination="false" :slidesPerView="4">
			<div class="swiper-slide swiper_homefind" slot="swiper-con" v-for="item in homefindArr">
				<Homefind :txt="item.title" :img="item.url"></Homefind>
			</div>
		</Bannerdemo>
		<Indexcounttile txt="为你推荐" class="title_classtwo"></Indexcounttile>
		<div class="content_wrap" v-for="item in recommendArr">
			<Indexitem :count="item.count" :point="item.point" mark="news1" :last="item.last" :writer="item.writer" :dels="item.del">
				<img :src="item.url" slot="img3"/>
			</Indexitem>
		</div>
		<div class="content_wrap" v-for="item in hotNews">
			<Indexitem :count="item.title" :point="item.content" mark="news1" last="" :writer="item.writer" dels="">
				
				<img :src="item.image" slot="img3"/>
			</Indexitem>
		</div>
		<!--<infinite-loading :on-infinite="onInfinite" ref="infiniteLoading"></infinite-loading >-->
	</div>
</template>
<script src="js/jquery-3.1.0.min.js"></script>
<script type="text/javascript">
	import InfiniteLoading from 'vue-infinite-loading'
	import request from "superagent"
	import jsonp from "superagent-jsonp"
	import Titles from "@/components/title.vue"
	import Indexcount from "@/components/indexcount.vue"
	import Indexcounttwo from "@/components/indexcounttwo.vue"
	import Doubantime from "@/components/doubantime.vue"
	import Indexcounttile from "@/components/indexcounttitle.vue"
	import Homefind from "@/components/homefind.vue"
	import Indexitem from "@/components/indexitem.vue"
	import axios from "axios"
	import Bannerdemo from "@/components/bannerdemo.vue"
	export default{
		components:{
			Titles,
			Indexcount,
			Indexitem,
			Indexcounttwo,
			Bannerdemo,
			Indexcounttile,
			Doubantime,
			Homefind,
			InfiniteLoading
		},
		data:function(){
			return{
				start:0,
				loadNumber:2,
				thisid:12331,
				picimg:"1",
				hotNews:[],
				nowNews:[],
				hottimeArr:[],
				weekgoArr:[],
				languageArr:[],
				radioArr:[],
				recommendArr:[],
				homefindArr:[]
			}
		},
		methods:{
			onInfinite() {
				var _this=this;
					setTimeout(function(){
						var temp=_this.hotNews.slice(_this.start,_this.start+_this.loadNumber);
						_this.nowNews=_this.nowNews.concat(temp);
						_this.$refs.infiniteLoading.$emit("$InfiniteLoading:loaded");
						if(_this.nowNews.length==_this.hotNews.length){
							_this.$refs.infiniteLoading.$emit("$InfiniteLoading:complete");
						}
						_this.start+=2;
						
					},2000)
				},
				getid:function(val){
					this.thisid=val;
				}
		},
		mounted(){
			var _this=this;
			
			this.$http.get("/static/data/test.json").then(function(res){
//				console.log(res.data);
			},function(res){
				
			});
			this.$http.get("/static/data/hottime.json").then(function(res){
				_this.hottimeArr=res.data.timething;
//				console.log(res.data.timething);
			},function(res){
				console.log(404);
			});
			this.$http.get("/static/data/weekGo.json").then(function(res){
				_this.weekgoArr=res.data.weekgo;
//				console.log(res.data.weekgo);
			},function(res){
				console.log(404);
			});
			this.$http.get("/static/data/monment.json").then(function(res){
				_this.languageArr=res.data.language;
//				console.log(res.data.language);
			},function(res){
				console.log(404);
			});
			this.$http.get("/static/data/radio.json").then(function(res){
				_this.radioArr=res.data.radios;
//				console.log(res.data.radios);
			},function(res){
				console.log(404);
			});
			this.$http.get("/static/data/recommend.json").then(function(res){
				_this.recommendArr=res.data.recommend;
//				console.log(res.data.recommend);
			},function(res){
				console.log(404);
			});
			this.$http.get("/static/data/homefind.json").then(function(res){
				_this.homefindArr=res.data.homefind;
//				console.log(res.data.homefind);
			},function(res){
				console.log(404);
			})
			
			
			
			
//			this.axios.get("/api/movie/subject/26363254").then(function(res){//网络请求电影
//				console.log(res.data)
//				_this.picimg=res.data.subjects[0].images.small;
//			}).catch(function(err){
//				console.log(err);
//			});
			
			
			
			
//			axios.get("/api/movie/subject/26363254").then(function(res){
//			axios.get("/api/movie/subject/26363254").then(function(res){//网络请求电影
//				console.log(res.data)
//				_this.picimg=res.data.subjects[0].images.small;
//			});
//-----------------------------
			request.get("https://api.douban.com/v2/event/list?loc=108288&start=0&count=4")
			.use(jsonp)
			.end((err,res)=>{
				if(!err){
				_this.hotNews=res.body.events
//				console.log(res.body.events)
				}
			});
		}
};
//	console.log(document.documentElement.clientWidth);
	document.documentElement.style.fontSize=document.documentElement.clientWidth/3.75+"px";
	
</script>

<style type="text/css">
			.titlewrap img{position: absolute;width:0.2rem;}
			.content_wrap img{width:0.87rem;height:0.87rem;margin-left:0.2rem;margin-top:0.04rem}
			.title_class{background:#fff;}
			.test span{font-size: 0.16rem;}
			.test h3{font-size: 0.16rem;}
			.test p{font-size: 0.14rem;}
			.title_classtwo h3{border-color: #42BD56!important;color:#000!important;}
			.title_classthree h3{border-color: transparent!important;color:#ccc!important;padding-left:0.2rem}
			.swiper_homefind{width:0.8rem!important;padding:0 0.1rem}
</style>		


