<!--很重要的轮播图封装组件；比较完善和灵活-->
<template>
	<div class="swiper-container" :class="swipeid">
		<div class="swiper-wrapper">
			<slot name="swiper-con"></slot>
		</div>
		<!--点击小点设置类名控制是否显示；和居中样式；-->
		<div :class='{"swiper-pagination":pagination}' :style="{'text-align': paginationDirection}"></div>
	</div>
</template>
<!--------------------------------------------------------------------------------->

<script>
	import "../../basic/js/swiper.min.js"
	export default{
		//将变量进行配置，有默认值也可在传入组件是改变；
		props:{
			swipeid:{
				type:String,
				default:""
			},
			effects:{
				type:String,
				default:"slide"
			},
			loop:{
				type:Boolean,
				default:true
			},
			paginationType:{
				type:String,
				default:"bullets"
			},
			pagination:{
				type:Boolean,
				default:true
			},
			paginationDirection:{
				type:String,
				default:"center"
			},
			direction:{
				type:String,
				default:"horizontal"
			},
			autoplay:{
				type:Number,
				default:5000
			},
			slidesPerView:{
				type:Number,
				default:1
			}
		},
		mounted:function(){
			var _that=this;
			new Swiper("."+_that.swipeid,{
					//设置配置项
					loop:_that.loop,
					effects:_that.effects,
					paginationType:_that.paginationType,
					pagination:'.swiper-pagination',
					direction:_that.direction,
					autoplay:_that.autoplay,
					autoplayDisableOnInteraction:false,
					slidesPerView:_that.slidesPerView,
					
					
					
					
					observer:true,
					observeParents:true
					
					
					
					
					
					
					
					
					
					
					
					
			})
		}
		
	}
	
</script>

<style>
	@import url("../../basic/css/swiper.min.css");
	.swiper-container{width:100%;}
	.swiper-wrapper{width:100%;height:1.2rem;}
	.swiper-wrapper .swiper-img{width:100%;height:1.2rem;}
	.swiper-wrapper .swiper-slide{width:100%;height:1.2rem;}
	.swiper-wrapper .swiper-slide img{width:100%}
	.swiper-wrapper .blue{background:blue}
	
</style>


