<template>
		<div class="bg_wrap" @click="fn">
				<slot name="texth2" class="texth2"></slot>
				<slot name="textspan"></slot>
				<slot name="textem"></slot>
				<!--<img src="../assets/img/01.jpg" v-show="ttt"/>
				<img src="../assets/img/04.jpg" v-show="!ttt"/>-->
				
				<slot name="bannerImg1" v-show="ttt" ></slot>
				<slot name="bannerImg2" v-show="!ttt"></slot>
		</div>
</template>
<script type="text/javascript">
	export default{
		data:function(){//数据部分
					return{
						ttt:false
					}
				},
		props:[],
		methods:{
			fn:function(){
				console.log(11);
				this.ttt=!this.ttt;
			}
		}
	}
	
</script>

<style type="text/css">
	.bg_wrap{height:1.2rem;width:100%;}
	.bg_wrap img{height:100%;width:100%}
	/*banner样式*/
</style>


