<template>
	<ul class="headerWrap">
		<!--第一种链接路由方法-->
		<!--<router-link to="/audios/movie"><Mynavli txts="电影" mark="movie" @change="getval" :sel="selector"></Mynavli></router-link>-->
		<Mynavli txts="电影" mark="movie" @change="getval" :sel="selector"></Mynavli>
		<Mynavli txts="读书" mark="read" @change="getval" :sel="selector"></Mynavli>
		<Mynavli txts="电视" mark="tv" @change="getval" :sel="selector"></Mynavli>
		<Mynavli txts="同城" mark="city" @change="getval" :sel="selector"></Mynavli>
		<Mynavli txts="音乐" mark="music" @change="getval" :sel="selector"></Mynavli>
	</ul>
</template>
<script type="text/javascript">
	import Mynavli from "./secondnavli.vue"
	export default {
		props:[],
		data:function(){
			return{
				selector:"movie"
			}
		},
		methods:{
			getval:function(val){
				this.selector=val;
				this.$router.push("/audios/"+this.selector);//两种链接二级路由方法；
			}
		},
		components:{
		   	Mynavli 
		}
	}
</script>
<style>
	.headerWrap{height:0.39rem;border:0.01rem solid #e4e4e4}
	/*导航部分样式*/
</style>


