<template>
	<div class="new1_wrap">
			<Titles txt="电影" class="titlewrap newstitle">
				<img src="../../assets/img/images/ic_seti_content_close.png"  slot="inputreturn" class="return"/>
			</Titles>
		
		<div>
			
			<ul class="list_wrap">
				<li class="clearfix">
					<div class="left">
						<img :src="nowArr.contentImg" />
					</div>
					<div class="right">
						<h3>{{nowArr.title}}</h3>
						<p>{{nowArr.score}}</p>
						<div class="stars">
							<img :src="nowArr.star1" slot="star" class="stars"/>
							<img :src="nowArr.star2" slot="star" class="stars"/>
							<img :src="nowArr.star3" slot="star" class="stars"/>
							<img :src="nowArr.star4" slot="star" class="stars"/>
							<img :src="nowArr.star5" slot="star" class="stars"/>
						</div>
					</div>
				</li>
				<div class="count">
						这部影片讲述的故事是这样的。。。。
				</div>
			</ul>
		</div>
	</div>
</template>
<script>
	import request from "superagent"
	import jsonp from "superagent-jsonp"
	import Titles from "@/components/title.vue"
	import axios from "axios"
	export default{
		components:{
			Titles

		},
		data:function(){
			return{
				movieArr:[],
				nowArr:{}
			}
		},
		methods:{

		},
		mounted(){
			var _this=this;
			this.$http.get("/static/data/movie.json").then(function(res){
				_this.movieArr=res.data.movie;
				_this.nowArr=res.data.movie[_this.$route.params.id-1];
				console.log(_this.$route.params.id-1)
				console.log(_this.nowArr)
			},function(res){
				console.log(404);
			})
		}
	}
</script>
<style>
	.new1_wrap .list_wrap{padding:0.1rem}
	.new1_wrap .list_wrap li{padding:0.2rem;border: 1px solid #ccc;}
	.new1_wrap .list_wrap .count{padding:0.2rem;border: 1px solid #ccc;font-size: 0.14rem;margin-top: 0.2rem;}
	.new1_wrap .list_wrap .left{float:left}
	.new1_wrap .list_wrap .left img{width:1rem}
	.new1_wrap .list_wrap .right{float:right}
	.new1_wrap .list_wrap .right .stars img{width:0.2rem;float: left;}
	.new1_wrap h3{font-size: 0.2rem;height:0.5rem;line-height: 0.5rem;text-align: center;}
	.new1_wrap p{font-size: 0.18rem;width:100%;display: block;text-align: center;height:0.3rem;line-height: 0.3rem;color:#EBC678}
	
</style>


