<template>
	<div class="new2_wrap">
			<Titles class="titlewrap" bol="true">
				<input type="text" class="search" slot="inputsearch"  placeholder="影视	图书	唱片	小组等" v-model="searchCount"/>
				<img src="../../assets/img/images/ic_seti_content_close.png" slot="backHome" class="imgfirst"/>
				<img src="../../assets/img/ic_search_gray.png" slot="searchDte" class="imgsecond" @click="searchThis"/>
				<img src="../../assets/img/ic_scan_gray.png" slot="Scan"  class="imgcheat">
		</Titles>
		<div class="searchcount">
			<h3>热门搜索</h3>
			<div class="historyList" >
				<ul class="clearfix">
					<div v-for="(item,index) in hotSearch" @click="add(index)">
						<Searchinput :name="item" ></Searchinput>
					</div>
				</ul>
			</div>
			<h3>历史记录</h3>
			<div class="historyList" >
				<ul class="clearfix">
					<div v-for="(item,index) in historySearch" @click="adds(index)">
						<Searchinput :name="item"></Searchinput>
					</div>
				</ul>
			</div>
			<h4 @click="clearHis">清空历史记录</h4>
		</div>
		
	</div>
</template>
<script>
	import Searchinput from "@/components/searchinput.vue"
	import request from "superagent"
	import jsonp from "superagent-jsonp"
	import Titles from "@/components/title.vue"
	import axios from "axios"
	export default{
		components:{
			Titles,
			Searchinput
		},
		data:function(){
			return{
				hotSearch:["战狼2","神奇女神","人民的名义","北京"],
				historySearch:["神奇女神","人民的名义","北京","神奇女神2","战狼2"],
				searchCount:"",
			}
		},
		methods:{
			clearHis:function(){
				this.historySearch.splice(0)
			},
			add:function(i){
				console.log(this.hotSearch[i]);
				this.searchCount=this.hotSearch[i];
			},
			adds:function(i){
				console.log(this.historySearch[i])
				this.searchCount=this.historySearch[i];
			},
			searchThis:function(){
				this.historySearch.push(this.searchCount);
				this.searchCount="";
			}

		},
		mounted(){
		
	}
}
</script>
<style>
	.new2_wrap .searchcount{padding:0.1rem;}
	.new2_wrap .historyList{padding:0.1rem;}
	.new2_wrap h3{height:0.6rem;line-height: 0.6rem;font-size: 0.18rem;color:#9E9E9E}
	.new2_wrap  h4{height:0.6rem;line-height: 0.6rem;font-size: 0.18rem;color:#9E9E9E;text-align: center;}
	.new2_wrap  .historyList ul{border-bottom: 0.01rem solid #ccc;}
	.new2_wrap  .historyList li{float: left;font-size: 0.16rem;height:0.3rem;line-height: 0.3rem;padding:0 0.1rem;border:1px solid #ccc;margin:0.1rem 0.1rem}
</style>


