<template>
	<div class="Allmovie">
		<Secondcount class="marginbot" text="电视"></Secondcount>
		<Bannerdemo swipeid="swipeidAudio" :autoplay="1000000" :pagination="false" class="swiper-down" :slidesPerView="4" style="background:#fff">
			<div class="down swiper-slide clearfix" slot="swiper-con" v-for="item in movieArr"> 
				<Myitemcount :txt="item.title" :score="item.score">
					<img :src="item.star1" slot="star" class="stars"/>
					<img :src="item.star2" slot="star" class="stars"/>
					<img :src="item.star3" slot="star" class="stars"/>
					<img :src="item.star4" slot="star" class="stars"/>
					<img :src="item.star5" slot="star" class="stars"/>
					<img :src="item.contentImg" slot="midImg"/>
				</Myitemcount>
			</div>
		</Bannerdemo>
		<Background>
			<img src="../../assets/img/1313_09.jpg" slot="bannerImg1" />
		</Background>
		<Bannertitle txt="演技太尴尬？欧阳娜娜自嘲是“靠实力失败”第一人"></Bannertitle>
		<!---->
		<Secondcount class="marginbot" text="院线即将上映"></Secondcount>
		<Bannerdemo swipeid="swipeidAudio" :autoplay="1000000" :pagination="false" class="swiper-down" :slidesPerView="4" style="background:#fff">
			<div class="down swiper-slide clearfix" slot="swiper-con" v-for="item in unmovieArr"> 
				<Myitemcount :txt="item.title" :score="item.score">
					<img :src="item.star1" slot="star" class="stars"/>
					<img :src="item.star2" slot="star" class="stars"/>
					<img :src="item.star3" slot="star" class="stars"/>
					<img :src="item.star4" slot="star" class="stars"/>
					<img :src="item.star5" slot="star" class="stars"/>
					<img :src="item.contentImg" slot="midImg"/>
				</Myitemcount>
			</div>
		</Bannerdemo>
		<Background>
			<img src="../../assets/img/IMG_0381_12.jpg" slot="bannerImg1" />
		</Background>
		<Bannertitle txt="《豆瓣收藏夹》预售中，一本满足你的收藏癖"></Bannertitle>
		<!---->
		<Secondcount class="marginbot" text="精选榜单"></Secondcount>
		<Bannerdemo swipeid="swipeidmove" :autoplay="1000000" :pagination="false" class="swiper-down seipeidmove" :slidesPerView="2" style="background:#fff">
			<div class="swiper-slide" slot="swiper-con"> 
				<img src="../../assets/img/IMG_03843_03.jpg" />
			</div>
			<div class="swiper-slide" slot="swiper-con"> 
				<img src="../../assets/img/IMG_03843_05.jpg" />
			</div>
			<div class="swiper-slide" slot="swiper-con"> 
				<img src="../../assets/img/IMG_03843_03.jpg" />
			</div>
			<div class="swiper-slide" slot="swiper-con"> 
				<img src="../../assets/img/IMG_03843_05.jpg" />
			</div>
		</Bannerdemo>
		<Secondcount class="marginbot" text="今日推荐影人"></Secondcount>
		<Bannerdemo swipeid="swipeidAudio" :autoplay="1000000" :pagination="false" class="swiper-down" :slidesPerView="4" style="background:#fff">
			<div class="down swiper-slide clearfix" slot="swiper-con" v-for="item in userArr"> 
				<Myitemcount :txt="item.title" :score="item.content">
					<img :src="item.contentImg" slot="midImg"/>
				</Myitemcount>
			</div>
		</Bannerdemo>
		<Secondcount class="marginbot" text="你可能感兴趣"></Secondcount>
		<div class="interAllWrap clearfix">
			<div class="interWrap"v-for="item in interArr"> 
				<Myitemcount :txt="item.title" :score="item.score">
					<img :src="item.star1" slot="star" class="stars"/>
					<img :src="item.star2" slot="star" class="stars"/>
					<img :src="item.star3" slot="star" class="stars"/>
					<img :src="item.star4" slot="star" class="stars"/>
					<img :src="item.star5" slot="star" class="stars"/>
					<img :src="item.contentImg" slot="midImg"/>
				</Myitemcount>
				
			</div>
			<div class="interWrap2">
				<a>查看更多</a>
			</div>
		</div>
		<Secondcount class="marginbot" text="最受欢迎影评"></Secondcount>
		<div class="content_wrap" v-for="item in movietalkArr">
			<Indexitem :count="item.title" :point="item.content" :writer="item.author"  :last="item.last">
				<img :src="item.url" slot="img3" style="height:1rem"/>
			</Indexitem>
		</div>
		
		
		
		
		
		
		
		
		
		
		
	</div>
</template>
<script type="text/javascript">
	import axios from "axios"
	import Secondnav from "@/components/secondnav.vue"
	import Secondcount from "@/components/secondcount.vue"
	import Background from "@/components/background.vue"
	import Myitemcount from "@/components/secondcount-item.vue"
	import Bannerdemo from "@/components/bannerdemo.vue"
	import Indexitem from "@/components/indexitem.vue"
	import Bannertitle from "@/components/bannertitle.vue"
	export default{
		components:{
			Secondnav,
			Secondcount,
			Background,
			Bannertitle,
			Myitemcount,
			Bannerdemo,
			Indexitem
		},
		data:function(){
			return{
				movieArr:[],
				unmovieArr:[],
				userArr:[],
				interArr:[],
				movietalkArr:[]
			}
		},
		mounted(){
			var _this=this;
			this.$http.get("/static/data/movie.json").then(function(res){
				_this.movieArr=res.data.movie;
				_this.unmovieArr=res.data.unmovie;
				_this.userArr=res.data.user;
				_this.interArr=res.data.inter;
				_this.movietalkArr=res.data.movietalk;
//				console.log(res.data.movie);
			},function(res){
				console.log(404);
			})
		}
	}
</script>

<style type="text/css">

	.marginbot{margin-bottom:0.4rem}
	.Allmovie .down{padding:0 0.1rem}
	.Allmovie .down .stars{width:0.1rem;height:0.1rem}
	.Allmovie .interWrap .stars{width:0.1rem;height:0.1rem}
	.Allmovie .interWrap2 a{display:block;height:0.8rem;line-height:0.8rem;font-size: 0.16rem;width:100%;text-align: center;color:#42bd56}
	.Allmovie .interWrap2{float:left;width:100%;}
	.Allmovie .swiper-down{height:2rem}
	.Allmovie .up a{color:#39b265;float:right;font-size: 0.14rem;}
	.Allmovie .seipeidmove .swiper-slide{margin:0 0.1rem}
	.marginbot{margin:0.1rem 0 0}
</style>


