<template>
		<div class="nav_wrap">
			<Itemtaber txt="主页" mark="home" :sel="selector" @change="getval">
				<img src="../assets/img/1.png" slot="normalImg">
				<img src="../assets/img/6.png" slot="activeImg">
			</Itemtaber>
			<Itemtaber txt="书影音" mark="audios" :sel="selector" @change="getval">
				<img src="../assets/img/2.png" slot="normalImg">
				<img src="../assets/img/7.png" slot="activeImg">
			</Itemtaber>
			<Itemtaber txt="广播" mark="room" :sel="selector" @change="getval">
				<img src="../assets/img/3.png" slot="normalImg">
				<img src="../assets/img/8.png" slot="activeImg">
			</Itemtaber>
			<Itemtaber txt="小组" mark="find" :sel="selector" @change="getval">
				<img src="../assets/img/4.png" slot="normalImg">
				<img src="../assets/img/9.png" slot="activeImg">
			</Itemtaber>
			<Itemtaber txt="我的" mark="user" :sel="selector" @change="getval">
				<img src="../assets/img/5.png" slot="normalImg">
				<img src="../assets/img/10.png" slot="activeImg">
			</Itemtaber>
		</div>
</template>
<script>
	import Itemtaber from './Itemtaber.vue';
	export default {
		data:function(){
			return{
				selector:"home",
			}
		},
		methods:{
			getval:function(val){
				this.selector=val;
				this.$router.push("/"+this.selector);//将当前点击的路径名字放入进去实现切换
//				console.log("selector:"+this.selector)
			}
		},
		components:{
		   	Itemtaber
		}
	}

</script>
<style>
	.nav_wrap{float:left;position: fixed;height:0.6rem;border-top:0.01rem solid #B6B6B6;bottom: 0rem;width:100%;background:#fff;z-index: 99;}
	
	/*底部样式*/
</style>
<!-- Add "scoped" attribute to limit CSS to this component only -->



