<template>
			<div class="tep2_wrap" :class="{tep_wrap:bol}">
				<!--<input type="text" class="search" :placeholder="msg"  v-show="bol"/>-->
				<div @click="returns"><slot name="inputreturn" class="return"></slot></div>
				<div @click="search"><slot name="inputsearch" class="search"></slot></div>
				<!--<img src="../assets/img/ic_search_gray.png" class="imgfirst" @click="fn" v-show="bol"/>-->
				<div @click="fn"><slot name="searchImg" class="imgfirst" ></slot></div>
				<div @click="returns"><slot name="backHome" class="imgfirst" ></slot></div>
				<!--<img src="../assets/img/ic_scan_gray.png" class="imgsecond" v-show="bol"/>-->
				<slot name="boxImg" class="imgsecond"></slot>
				<slot name="searchDte" class="imgsecond"></slot>
				<!--<img src="../assets/img/ic_chat_white.png" class="imgthird" v-show="bol"/>-->
				<h3>{{txt}}</h3>
				<slot name="img1" class="imgsearch"></slot>
				<slot name="img2" class="imgcheat"></slot>
				<slot name="Scan" class="imgcheat"></slot>
			</div>
</template>
<script type="text/javascript">
	export default{
		data:function(){//数据部分
			return{
				msg:"请输入搜索内容"
			}
		},
		props:["txt","bol"],
		methods:{//函数方法部分；
			fn:function(){
				console.log(1);
				this.msg="输入内容不对"
			},
			returns:function(){

				this.$router.push("/");
			},
			search:function(){
				this.$router.push("/news2");
			}
		},
		components:{
			
		}
	}
</script>
<style type="text/css">
			.tep2_wrap{height:0.45rem;background:#eee;position: relative;width:100%;text-align: center;line-height: 0.45rem;border-bottom: 0.01rem solid #ccc;}
			.tep_wrap{height:0.45rem;background:#48C05A;position: relative;width:100%;text-align:left;padding-top: 0.1rem;}
			.tep_wrap .search{width:83%;height:0.3rem;border-radius: 0.05rem;border:none;text-indent: 2em;margin-left:0.1rem;color:#D2D3D2;display: block;}
			.tep_wrap .imgsearch{right:0.8rem;top:0.15rem}
			.tep2_wrap .return{left:0.2rem;top:0.1rem;width:0.3rem}
			.tep_wrap .imgcheat{right:0.2rem;top:0.15rem}
			.tep_wrap img{position: absolute;width:0.2rem;}
			.tep_wrap .imgfirst{left:0.15rem;top:0.15rem}
			.tep_wrap .imgsecond{right:0.6rem;top:0.15rem}
			.tep_wrap .imgthird{right:0.2rem;top:0.1rem;width:0.3rem}
			.tep2_wrap h3{font-size: 0.2rem;}
			/*头部样式*/
</style>


