<template>
	<div class="new1_wrap">
		<div v-for="item in nowNew">
			<Titles :txt="item.title" class="titlewrap newstitle">
				<img src="../../assets/img/images/ic_seti_content_close.png"  slot="inputreturn" class="return"/>
			</Titles>
			<ul class="list_wrap">
				<li >
					<h3>{{item.title}}</h3>
					<p v-html="item.content"></p>
					<img :src="item.image" />
				</li>
			</ul>
		</div>
	</div>
</template>
<script>
	import request from "superagent"
	import jsonp from "superagent-jsonp"
	import Titles from "@/components/title.vue"
	import axios from "axios"
	export default{
		components:{
			Titles

		},
		data:function(){
			return{
				hotNew:[],
				nowNew:[]
			}
		},
		methods:{

		},
		mounted(){
		var _this=this;
//		console.log(this.$route.params.id);
//		request.get("https://api.douban.com/v2/event/list?loc=108288&start=0&count=10")
		request.get("https://api.douban.com/v2/event/"+this.$route.params.id)
		.use(jsonp)
		.end((err,res)=>{
			if(!err){
				console.log(res)
//			_this.hotNew=res.body.events;
			_this.nowNew.push(res.body);
				console.log(_this.$route.params.id)
//				console.log(_this.nowNew)
//				console.log(res.body.events)
			}
		});
	}
}
</script>
<style>
	.new1_wrap .list_wrap{padding:0.1rem}
	.new1_wrap h3{font-size: 0.24rem;}
	.new1_wrap p{font-size: 0.2rem;width:100%;display: block;}
	.newstitle h3{width:60%;overflow: hidden;height:46px;margin: 0 auto;white-space: nowrap;text-overflow: ellipsis;}
	
</style>


