<template>
	<div class="find_wraps">
		<Titles txt="小组" class="titlewrap">
				<img src="../../assets/img/ic_search_green.png" slot="img1" class="imgsearch">
				<img src="../../assets/img/ic_chat_green.png" slot="img2" class="imgcheat">
		</Titles>
		<Background class="bgground">
			<h2 slot="texth2" class="texth2">41231个小组等着你来玩</h2>
			<span slot="textspan" class="textspan">立即选择加入吧</span>
			<span slot="textspan" class="textem">根据你的兴趣推荐</span>
			<img src="../../assets/img/images/ic_group_banner.png" slot="bannerImg1" />
		</Background>
		<Background class="bgground_two">
			<span slot="textspan" class="textem">精选推荐</span>
			<img src="../../assets/img/images/ic_rec_group_banner_6.png" slot="bannerImg1" />
		</Background>
		<Middletitle text="影视"></Middletitle>
		<div v-for="item in findArr">
			<Findcount :num="item.num" :title="item.title" :text="item.content">
				<img :src="item.url" slot="headImg" class="imgpic"/>
			</Findcount>
		</div>
		<Middletitle text="读书"></Middletitle>
		<div v-for="item in findArr">
			<Findcount :num="item.num" :title="item.title" :text="item.content">
				<img :src="item.url" slot="headImg" class="imgpic"/>
			</Findcount>
		</div>
		<Middletitle text="音乐"></Middletitle>
		<div v-for="item in findArr">
			<Findcount :num="item.num" :title="item.title" :text="item.content">
				<img :src="item.url" slot="headImg" class="imgpic"/>
			</Findcount>
		</div><Middletitle text="同城"></Middletitle>
		<div v-for="item in findArr">
			<Findcount :num="item.num" :title="item.title" :text="item.content">
				<img :src="item.url" slot="headImg" class="imgpic"/>
			</Findcount>
		</div>
		<Middletitle text="高校"></Middletitle>
		<div v-for="item in findArr">
			<Findcount :num="item.num" :title="item.title" :text="item.content">
				<img :src="item.url" slot="headImg" class="imgpic"/>
			</Findcount>
		</div>
		
		<Footnav text="选好了，去玩小组"></Footnav>
		
	</div>
</template>
<script type="text/javascript">
	import Titles from "@/components/title.vue"
	import Background from "@/components/background.vue"
	import Findcount from "@/components/find-count.vue"
	import Footnav from "@/components/footnav.vue"
	import axios from "axios"
	import Middletitle from "@/components/middletitle.vue"
	export default{
//		name:"Fy",
		components:{
			Titles,
			Background,
			Findcount,
			Footnav,
			Middletitle
		},
		data:function(){
			return{
				findArr:[]
			}
		},
		mounted(){
			var _this=this;
			this.$http.get("/static/data/finditem.json").then(function(res){
				_this.findArr=res.data.finditem;
//					console.log(res.data.finditem);
			},function(res){
				console.log(404);
			})
			
			
			
		}
	}	
</script>

<style type="text/css">
			.titlewrap .imgsearch{right:0.8rem;top:0.15rem}
			.titlewrap .imgcheat{right:0.2rem;top:0.15rem}
			.titlewrap img{position: absolute;width:0.2rem;}
			.find_wraps{background:#fff;padding-bottom: 0.47rem;}
			.find_wraps .bgground{height:1.54rem;margin-bottom: 0.15rem;position: relative;}
			.find_wraps .bgground .texth2{height:0.35rem;line-height:0.35rem;position: absolute;left: 0.55rem;top:0.5rem;font-size: 0.25rem;}
			.find_wraps .bgground .textspan{position: absolute;left: 1.3rem;top:0.8rem;color:#9f9eb0;height:0.25rem;line-height: 0.25rem;font-size: 0.16rem;}
			.find_wraps .bgground .textem{position: absolute;left: 0rem;bottom:0rem;color:#9f9eb0;height:0.2rem;line-height: 0.2rem;font-size: 0.14rem;}
			.find_wraps .bgground_two{height:0.75rem;width:95%;margin:0rem auto;position: relative;}
			.find_wraps .bgground_two .textem{position: absolute;left: 0.12rem;bottom:0.05rem;color:#cbffd5;height:0.25rem;line-height: 0.25rem;font-size: 0.16rem;}
</style>


