<template>
	<div>
		<Titles txt="广播" class="titlewrap">
				<img src="../../assets/img/images/ic_status_search_user.png" slot="img1" class="imgsearch">
				<img src="../../assets/img/ic_chat_green.png" slot="img2" class="imgcheat">
		</Titles>
		
		<Roomlogo text="关注他们，发现更大的世界" title="发现有趣的人"></Roomlogo>
		<div v-for="item in roomArr">
			<Roomitem :name="item.name" :text="item.text" :count="item.content" :check="item.check" :img="item.url"></Roomitem>
		</div>
		
		
		
	</div>
</template>
<script type="text/javascript">
	import Titles from "@/components/title.vue"
	import axios from "axios"
	import Roomitem from "@/components/room-item.vue"
	import Roomlogo from "@/components/room-log.vue"
	export default{
		
		components:{
			Titles,
			Roomitem,
			Roomlogo
		},
		data:function(){
			return{
				roomArr:[]
			}
		},
		mounted(){
			var _this=this;
			
			this.$http.get("/static/data/roomdata.json").then(function(res){
				_this.roomArr=res.data.roomdata;
//				console.log(res.data);
			},function(res){
				
			});
		}
	}
	
</script>

<style type="text/css">
			/*body{background:#F7F7F7}*/
			.titlewrap .imgsearch{right:0.8rem;top:0.15rem}
			.titlewrap .imgcheat{right:0.2rem;top:0.15rem}
			.titlewrap img{position: absolute;width:0.2rem;}
</style>


